import 'isomorphic-fetch';

import Link from 'next/link';
import Layout from '../src/components/layout';
import { Component } from 'react';

export default class extends Component {
    static async getInitialProps() {
        const request = await fetch ('https://api.audioboom.com/channels/recommended')
        const { body : channels } = await request.json();
        return { channels };
    }
    render() {
        const { channels } = this.props

        return <Layout title="Podcasts">
                    <div className="channels">
                        { channels.map((channel) => (
                            <Link href={ `/channel?id=${ channel.id }` }>
                                <a className="channel">
                                    <img src={ channel.urls.logo_image.original } alt="cover"/>
                                    <h2> { channel.title } </h2>
                                </a>
                            </Link>
                        )) }
                    </div>
                <style jsx>{`
                    .channels {
                        display: grid;
                        grid-gap: 15px;
                        padding: 15px;
                        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
                    }
                    .channels a {
                        text-decoration: none;
                    }
                    .channel img {
                        border-radius: 3px;
                        box-shadow: 0px 2px 6px rgba(0,0,0,0.15);
                        width: 100%;
                    }
                    h2 {
                        padding: 5px;
                        font-size: 0.9em;
                        font-weight: 600;
                        margin: 0;
                        text-align: center;
                    }
                `}</style>
            </Layout>
    }
}


